export default {
	data() {
		return {
			PageCur: '我的',
			userinfo:{},
		};
	},
	onLoad() {
		//加载
		this.init();
	},
	methods: {
		init() {
			//用户信息
			this.userinfo={
				face:'../../static/HM-PersonalCenter/face.jpeg',
				username:"游客",
				integral:"请登录进入社区"
			}		
		},
		//用户点击订单类型
		toOrderType(index){
			uni.showToast({title: this.orderTypeLise[index].name});
		},
		//用户点击列表项
		toPage(list_i,li_i){
			uni.showToast({title: this.severList[list_i][li_i].name});
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
	}
}
